import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';

import {
    ViewWidth, IOS, View, PanelHeader,
    Group, ContentCard, Title, Text,
    Gradient, Div, Button, Search,
    Spacing, Placeholder, ModalRoot,
	ModalPage, ModalPageHeader, 
    PanelHeaderClose, PanelHeaderButton,
    Panel, Banner, FixedLayout, ScreenSpinner
} from '@vkontakte/vkui';

import {
    Icon16SadFaceOutline, Icon28HideCircleFillBlack, Icon24Dismiss, Icon16LogoAndroid
} from '@vkontakte/icons';

// { id: 81, img: "", maxImg: 150, date: "", theme: "", work: "Домашнее задание: § «» №" },
// №
// «»
// §

const thematics = [
    { id: 1, img: "", maxImg: 150, date: "Русский язык 04.09.2021", theme: "Обращение и знаки препинания при нём", work: "Домашнее задание: 36§, Учить 198-199, упр. 425 (закончить), упр. 426" },
];

// Модальные карточки
const MODAL_MORE = 'fullscreen';

class Work extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            panel: 'page1',
            search: '',
            viewBanner: true,
            activeModal: null,
            modalHistory: []
        }
        this.onChange = this.onChange.bind(this);

        this.modalBack = () => {
			this.setActiveModal(this.state.modalHistory[this.state.modalHistory.length - 2]);
		};
    }

    onChange(e) { this.setState({ search: e.target.value }); }

    get thematics() {
        const search = this.state.search.toLowerCase();
        return thematics.filter(({ date }) => date.toLowerCase().indexOf(search) > -1);
    }

    setActiveModal(activeModal) {
		activeModal = activeModal || null;
		let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];

		if (activeModal === null) {
			modalHistory = [];
		} else if (modalHistory.indexOf(activeModal) !== -1) {
			modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
		} else {
			modalHistory.push(activeModal);
		}

		this.setState({
			activeModal,
			modalHistory
		});
	};

    render() {
        const isMobile = this.props.viewWidth <= ViewWidth.MOBILE;
		const platform = this.props.platform;

        return (
            <View modal={modal} activePanel={this.state.panel}>

            <Panel id="page1">
            <React.Fragment>
                <PanelHeader>Домашнее задание</PanelHeader>
                
                <Group>
                    <FixedLayout vertical="top">
                        <Search placeholder="Русский язык 16.04.2021" value={this.state.search} onChange={this.onChange} after={null} />
                    </FixedLayout>

                    <Spacing size={40} />

                    {this.state.viewBanner === true && 
                    <React.Fragment>
                        <Banner
                            before={<Icon28HideCircleFillBlack height={44} width={44} fill="red" />}
                            header="Доступна только данная вкладка?"
                            asideMode="dismiss"
                            onDismiss={() => this.setState({viewBanner: false}) }
                            actions={<Button mode="tertiary" onClick={() => this.setActiveModal(MODAL_MORE)}  hasHover={false}>Узнать подробнее</Button>}
                        /> 
                    <Spacing size={10} />
                    </React.Fragment>}

                    {this.thematics.length > 0 && this.thematics.map(thematic =>
                        <ContentCard
                            style={{ "margin-bottom": '11px', "margin-left": '16px', "margin-right": '16px'}}
                            subtitle={thematic.date}
                            header={thematic.theme}
                            caption={thematic.work}
                            image={thematic.img}
                            maxHeight={thematic.maxImg}
                            key={thematic.id}>
                        </ContentCard>
                    )}

                    {this.thematics.length === 0 && <Placeholder icon={<Icon16SadFaceOutline fill="#3883A8" width={56} height={56} />}
                        header="Ничего не найдено">
                        Возможно, Вы допустили ошибку в запросе
                    </Placeholder>}

                    {this.thematics.length > 0 && <Spacing size={40} />}
                </Group>
            </React.Fragment>
            </Panel>
            
            </View>
        );
    }
}

export default Work;