import React from 'react';
import PropTypes from 'prop-types';
import bridge from '@vkontakte/vk-bridge';

import { 
	Panel, 
	PanelHeader, 
	Group,
	Div,
	Header,
	IconButton,
	Snackbar,
} from '@vkontakte/vkui';

import { 
	Icon24Copy,
	Icon28HomeOutline,
	Icon28BillheadOutline,
	Icon28Menu,
	Icon32CheckCircle,
} from '@vkontakte/icons';

class ScheduleTimes extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			text: '',
			snackbar: null
		};
		this.openWithAvatar = this.openWithAvatar.bind(this);
	}

	openWithAvatar() {
		if (this.state.snackbar) return;
		bridge.send("VKWebAppCopyText", {
			text: `
			Расписание звонков 8 класса:
			
			1. 8:55 — 9:35
			2. 9:45 — 10:25
			3. 10:35 — 11:15
			4. 11:35 — 12:15
			5. 12:25 — 13:05
			6. 13:15 — 13:55
			7. 14:05 — 14:45`})
		this.setState({
			snackbar:
				<Snackbar
					onClose={() => this.setState({ snackbar: null })}
					style={{"padding": '49px 2px'}}
					onActionClick={() => this.setState({ text: 'Скопировано' })}
					after={<Icon32CheckCircle fill="#3883A8" />}
				>
					Скопировано в буфер обмена
				</Snackbar>
		});
	}

	render() {
		return (
			<Panel> <PanelHeader>Расписание звонков</PanelHeader>
				<Group>
					<Header style={{ "bottom-left": '19px' }} mode="secondary">Расписание звонков</Header>
					<Div>
						1. 8:55 — 9:35 <br />2. 9:45 — 10:25 <br />3. 10:35 — 11:15 <br />4. 11:35 — 12:15 <br />5. 12:25 — 13:05 <br />6. 13:15 — 13:55

				<IconButton style={{ "float": 'right' }} alt="Скопировать расписание" onClick={this.openWithAvatar}>
							<Icon24Copy fill="#3883A8" />
						</IconButton>
						<br />7. 14:05 — 14:45
					</Div>
					{this.state.text && <Div>{this.state.text}</Div>}
				</Group>

				{this.state.snackbar}
			</Panel>
		)
	}
}

export default ScheduleTimes;